<template>
  <div v-theme:column="'wide'" id="show-blogs">
      <h1>博客总览</h1>
      <input type="text" placeholder="搜索" v-model="search">
      <div class="single-blog" v-for="blog in filteredBlogs" :key="blog.objectId">
        <router-link v-bind:to="'/blog/'+blog.objectId">
          <h2 v-rainbow>{{blog.title | to-upper-case}}</h2>
          <article>
              {{blog.body | snippet}}
          </article>
          </router-link>
      </div>
  </div>
</template>

<script>
export default {
  name: 'ShowBlogs',
  props: {
  },
  data(){
      return {
          blogs:[],
          search:"",  
      }
  },
  created(){
      this.$http.get("myblogs")
      .then((data)=>{
        console.log(data);
        this.blogs = data.body.results.slice(0,10);
      })    
  },
  computed:{
      filteredBlogs:function(){
        return this.blogs.filter((blog)=>{
            return blog.title.toUpperCase().match(this.search.toUpperCase());
        })
      }
  },
  directives:{
      'rainbow':{
            bind(el,binding,vnode){
                el.style.color="#"+Math.random().toString().substr(2,8);
            }
      }
  },
  filters:{
      toUpperCase(value){
           return value.toUpperCase();
      }
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#show-blogs{
    max-width: 800px;
    margin: 0 auto;
}
.single-blog{
    padding: 20px;
    margin: 20px 0;
    box-sizing: border-box;
    background: #eee;
}
.show-blogs a{
    color:#444;
    text-decoration: none;
}
input[type='text']{
    padding: 8px;
    width: 100%;
    box-sizing: border-box;
}

</style>
