<template>
  <div id="single-blog">
      <h1>{{blog.title}}</h1>
      <article>{{blog.body}}</article>
  </div>
</template>

<script>
export default {
  name: 'SingleBlog',
  props: {
  },
  data(){
      return{
          id:this.$route.params.id,
          blog:{}
      }
  },
  created(){
      this.$http.get("myblogs/"
      +this.id)
      .then((data)=>{
          this.blog=data.body;
      })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#single-blog{
    max-width: 960px;
    margin: 0 auto;
    padding:20px;
    background: #eee;
    border: 1px dotted #aaa;
}
</style>
